.control-panel {
    grid-row: 3 / 5;
    grid-column: 4 / 10;
    border: var(--border);
    background-color: var(--shadedDark);
    padding: 1.5rem;
    @extend .vertical-align;
    justify-content: space-between;
    //to position reset button inside player form
    position: relative;

    @media screen and (max-width: 1175px) {
        margin-bottom: var(--gutter);
        height: 30vh;
    }

    @media screen and (max-width: 768px) {
        //since viewport height is bad on mobile due to the URL bar
        height: 400px;
        justify-content: center;
    }

}

.select-number, .customise {
    display: flex;
    align-items: center;
}

.select-number {

    p {
        margin-right: 0.8rem;
    }
    
    button:last-child {
        margin-left: 0.5rem;
    }
    
}

//'balance teams by skill' and 'delete players' pop ups
.control-message {
    //position message centrally in control panel 
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    @extend .vertical-align;

    input {
        width: 20px;
        height: 20px;
    }

    @media screen and (max-width: 768px) {
        position: static;
        transform: none;
        margin-top: 2rem;
    }
}

//position generate teams button in control panel
.btn-generate {
    position: absolute;
    bottom: 1.5rem;
    left: calc(1.2rem + 1.5rem);

    @media screen and (max-width: 768px) {
        position: static;
    }
}

@media screen and (max-width: 768px) {
    .form-generate {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .customise {
        justify-content: center;
    }
}