.team-list-1-final {
    grid-row: 3 / 7;
    grid-column: 4 / 7;
}

.team-list-2-final {
    grid-row: 3 / 7;
    grid-column: 7 / 10;
}

.team-list-1-final, .team-list-2-final {
    background-color: var(--shadedDark);
    border: var(--border);
    padding: 2rem;
    @extend .vertical-align;
    justify-content: space-between;

    @media screen and (max-width: 1175px) {
        padding: 2rem 3rem;
        margin-bottom: var(--gutter);
    }

    @media screen and (max-width: 768px) {
        padding: 2rem 1rem;
    }

    ul {
        width: 100%;
        max-height: 60%;

        @media screen and (max-width: 1175px) {
            //since viewport height is bad on mobile due to the URL bar
            max-height: 230px;
        }
    }
}